import "./App.css";
import Nav from "./components/Nav";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
import React, { Suspense, useState } from "react";
import Cookies from "js-cookie";
import { SetResultsFromCookie } from "./util/CookieResultManager";

const Bracket = React.lazy(() => {
  return new Promise((resolve) => {
    setTimeout(() => resolve(import("./components/Bracket/Bracket")), 500);
  });
});
const GroupStage = React.lazy(() =>
  import("./components/GroupStage/GroupStage")
);

const App = () => {
  const result = Cookies.get("result");
  const groupsData = SetResultsFromCookie(result);
  const [loadBracket, setLoadBracket] = useState(
    groupsData.teamsSelected === 16
  );

  return (
    <div className="App relative text-center bg-slate-200 overflow-hidden">
      <Nav />
      <div className="mt-11"></div>
      {loadBracket ? null : (
        <Suspense
          fallback={
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
              }}
            >
              <CircularProgress />
            </Box>
          }
        >
          <GroupStage groupsData={groupsData} setLoadBracket={setLoadBracket} />
        </Suspense>
      )}
      {loadBracket ? (
        <Suspense
          fallback={
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
              }}
            >
              <CircularProgress />
            </Box>
          }
        >
          <Bracket />
        </Suspense>
      ) : null}
    </div>
  );
};

export default App;
